import React from 'react'
import style from './style.module.css'
import Week from '../weekStream';
import queryCountData from '../../../../../redux/reducers/countData';
import { Collapse, Progress } from 'antd';
const { Panel } = Collapse;
class index extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      list: queryCountData('', { type: 'getCountData', data: { list: 'all', demand: { date: [this.props.timeSeg.start, this.props.timeSeg.end] } } }),
      end: this.props.timeSeg.end,
      start: this.props.timeSeg.start
    }
    this.timeSeg = []
  }

  // 对时间按星期划分
  splitAsWeek = () => {
    // 找到该月的第一天与最后一天最后一刻的毫秒数
    let s = this.state.start
    let e = this.state.end
    e = new Date(e).getMonth() + 1
    e = new Date(new Date(s).getFullYear(), e, 1)
    e = new Date(e.getTime() - 1)
    console.log(new Date(e));
    // 按周进行划分
    let i = 0
    while (s < e) {
      this.timeSeg[i] = { start: e - 86400000 * 7, end: e }
      e = this.timeSeg[i].start
      i++
    }
    this.timeSeg[--i].start = new Date(new Date(s).getFullYear(), new Date(s).getMonth(), 1).getTime()
    let x = this.timeSeg
    console.log(x);
    return this.renderWeeks()
  }
  renderWeeks = () => {
    console.log(111);
  }
  // 周结余头部
  renderHead = () => {
    return (
      <div className={style.collapsHead}>
        <div style={{ width: '120px', display: 'flex', justifyContent: 'center', height: '60px', paddingTop: '4px' }}>
          <div >
            <div>
              <span style={{ fontSize: '25px', color: 'black', fontWeight: 600 }}>5</span>
              <span style={{ fontSize: '10px', color: '#787979' }}>周</span>
            </div>
            <div style={{ marginTop: '-10px', fontSize: '10px', color: '#787979' }}>05.24-05.30</div>
          </div>
        </div>
        <div className={`${style.headDisplay} monthBalance`} >
          <div>
            <div style={{ color: '#e06c62', display: 'flex', flexDirection: 'column' }}>
              <span>收 6.00</span>
              <Progress percent={50} showInfo={false} size="small" strokeColor='#e06c62' />
            </div>
            <div style={{ color: '#56b78c', display: 'flex', flexDirection: 'column' }}>
              <span>支 101.00</span>
              <Progress percent={50} showInfo={false} size="small" strokeColor='#56b78c' />
            </div>
          </div>
          <div className='balanceA'>
            <div style={{ color: '#787979' }}>结余</div>
            <div style={{ color: 'black', fontWeight: 400, fontSize: '20px', marginTop: '-10px' }}>-95.00</div>
            <Progress percent={50} showInfo={false} size="small" strokeColor='rgb(209, 209, 209)' />
          </div>
        </div>
      </div>
    )
  }
  render () {
    return (
      <div>
        {/* 预算 */}
        <div className={style.progress}>
          <div style={{ display: 'flex', justifyContent: 'space-between' }}>
            <span>本月预算 {JSON.parse(localStorage.getItem('balance')) || 0}</span>
            <span>余额 <span style={{ color: '#e37e00', fontWeight: 500 }}>123</span></span>
          </div>
          <div className='balanceProgress'>
            <Progress percent={50} showInfo={false} size="small" strokeColor='orange' />
          </div>
        </div>
        {this.splitAsWeek()}
        <Collapse accordion className='site_collapse_custom_collapse'>
          <Panel header={this.renderHead()} key="1" showArrow={false} className='site_collapse_custom_panel'>
            <Week timeSeg={this.timeSeg[4]}></Week>
          </Panel>
        </Collapse>
      </div>
    );
  }
}

export default index;
